/* src/styles/Services.css */

.services-wrapper {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  max-width: 800px;
  margin: 40px auto;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.08);
  background-color: #181717;
}

.services-box {
  padding: 24px;
  background-color: #fff;
  border-bottom: 1px solid #eaeaea;
}

.services-box:last-child {
  border-bottom: none;
}

.services-header {
  padding: 0;
  height: 250px;
  overflow: hidden;
}

.services-header img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

/* Middle Section */
.services-middle h2 {
  margin-bottom: 0.25rem;
  font-size: 1.5rem;
}

.tap-pay-text {
  color: #6c757d;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.services-middle .links {
  margin-top: 24px;
  display: flex;
  justify-content: center;
  gap: 60px;
  font-size: 1rem;
}

.services-middle .links a {
  text-decoration: none;
  color: #18181a;
  font-weight: 600;
  transition: color 0.3s ease;
}

.services-middle .links a:hover {
  color: #007bff;
}

/* Form Styling */
.services-form,
.services-lower form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  margin-top: 16px;
}

.services-form input,
.services-lower input {
  width: 100%;
  max-width: 300px;
  padding: 12px;
  font-size: 1rem;
  border: 1px solid #ced4da;
  border-radius: 6px;
  transition: border-color 0.3s ease;
}

.services-form input:focus,
.services-lower input:focus {
  border-color: #007bff;
  outline: none;
}

.services-form button,
.payment-options button {
  padding: 12px 24px;
  font-size: 1rem;
  background-color: #18181a;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.services-form button:hover,
.payment-options button:hover {
  background-color: #444;
}

/* Payment Options */
.payment-options {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin-top: 20px;
}

.payment-images {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 50px;
  margin-bottom: 10px;
}

.payment-img {
  width: 100px;
  height: auto;
}

.payment-buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  width: 100%;
}

/* MOMO and Bank Buttons */
.momo-button,
.bankcard-button {
  width: 200px;
  padding: 12px 24px;
  font-size: 1rem;
  font-weight: 600;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.momo-button {
  background-color: #FFD700;
  color: #000;
}

.momo-button:hover {
  background-color: #FFC300;
  transform: translateY(-2px);
}

.bankcard-button {
  background-color: #007bff;
  color: #fff;
}

.bankcard-button:hover {
  background-color: #0056b3;
  transform: translateY(-2px);
}

.token-message {
  font-size: 0.95rem;
  color: #495057;
  text-align: center;
  margin-top: 20px;
}

/* General Info Link */
.general-info {
  text-decoration: none;
  color: #007bff;
  font-weight: bold;
}

.general-info:hover {
  color: #0056b3;
}


/* Navigation Tabs Styling */
.nav-tab {
  cursor: pointer;
  font-weight: 600;
  color: #18181a;
  transition: color 0.3s ease;
}

.nav-tab:hover {
  color: #007bff;
}

.nav-tab.active {
  color: #007bff;
}
